import TeslaCounter from '../components/TeslaCounter/TeslaCounter';
import { connect } from 'react-redux';
import { temperatureUp, temperatureDown } from '../actions';
import { DEFAULT_VALUES } from '../constants';

const mapStateToProps = ({ config }) => ({
  currentValue: config.temperature,
  initValues: DEFAULT_VALUES.temperature
});

const mapDispatchToProps = dispatch => ({
  increment: (e, value) => {
    e.preventDefault();
    dispatch(temperatureUp(value))
  },
  decrement: (e, value) => {
    e.preventDefault();
    dispatch(temperatureDown(value))
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(TeslaCounter);