import TeslaCounter from '../components/TeslaCounter/TeslaCounter';
import { connect } from 'react-redux';
import { speedUp, speedDown } from '../actions';
import { DEFAULT_VALUES } from '../constants';

const mapStateToProps = ({ config }) => ({
  currentValue: config.speed,
  initValues: DEFAULT_VALUES.speed
});

const mapDispatchToProps = dispatch => ({
  increment: (e, value) => {
    e.preventDefault();
    dispatch(speedUp(value))
  },
  decrement: (e, value) => {
    e.preventDefault();
    dispatch(speedDown(value))
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(TeslaCounter);