import { DEFAULT_VALUES, ACTIONS } from '../constants';

export const speedUp = (value) => ({
  type: ACTIONS.SPEED_UP,
  value,
  step: DEFAULT_VALUES.speed.step,
  maxValue: DEFAULT_VALUES.speed.max
});

export const speedDown = (value) => ({
  type: ACTIONS.SPEED_DOWN,
  value,
  step: DEFAULT_VALUES.speed.step,
  minValue: DEFAULT_VALUES.speed.min
});

export const temperatureUp = (value) => ({
  type: ACTIONS.TEMPERATURE_UP,
  value,
  step: DEFAULT_VALUES.temperature.step,
  maxValue: DEFAULT_VALUES.temperature.max
});

export const temperatureDown = (value) => ({
  type: ACTIONS.TEMPERATURE_DOWN,
  value,
  step: DEFAULT_VALUES.temperature.step,
  minValue: DEFAULT_VALUES.temperature.min
});

export const changeClimate = () => ({
  type: ACTIONS.CHANGE_CLIMATE
});

export const changeWheel = (value) => ({
  type: ACTIONS.CHANGE_WHEEL,
  value
});

export const updateStats = () => ({
  type: ACTIONS.UPDATE_STATS
});