import { getModelData } from '../services/BatteryService';
import { ACTIONS } from '../constants';

const dataModels = getModelData();
const carModels = ['60', '60D', '75', '75D', '90D', 'P100D'];

function updateStats(state, newState) {
  return {
    ...state,
    config: newState.config,
    carStats: calculateStats(newState)
  }
}

function calculateStats(state) {
  const {speed, temperature, climate, wheelSize} = state.config;
    
  return carModels.map(model => {
    const miles = dataModels[model][wheelSize][climate ? 'on' : 'off'].speed[speed][temperature];

    return {
      model,
      miles
    }
  });
}

const increment = (state, action, key) => {
  const { value, step, maxValue } = action;

  if (state.config[key] >= maxValue) return state;

  return updateStats(state, {
    ...state,
    config: {
      ...state.config,
      [key]: value + step
    }
  });
};

const decrement = (state, action, key) => {
  const { value, step, minValue } = action;

  if (state.config[key] <= minValue) return state;

  return updateStats(state, {
    ...state,
    config: {
      ...state.config,
      [key]: value - step
    }
  });
};

const initialConfig = {
  speed: 45,
  temperature: 10,
  climate: false,
  wheelSize: 21
};

const initialState = {
  carStats: calculateStats({
    config: initialConfig
  }),
  config: initialConfig
}

function appReducer(state = initialState, action) {
  switch(action.type) {
    case ACTIONS.SPEED_UP: {
      return increment(state, action, 'speed');
    }
    case ACTIONS.SPEED_DOWN: {
      return decrement(state, action, 'speed');
    }
    case ACTIONS.TEMPERATURE_UP: {
      return increment(state, action, 'temperature');
    }
    case ACTIONS.TEMPERATURE_DOWN: {
      return decrement(state, action, 'temperature')
    }
    case ACTIONS.CHANGE_CLIMATE: {
      return updateStats(state, {
        ...state,
        config: {
          ...state.config,
          climate: !state.config.climate
        }
      });
    }
    case ACTIONS.CHANGE_WHEEL: {
      return updateStats(state, {
        ...state,
        config: {
          ...state.config,
          wheelSize: action.value
        }
      });
    }
    default:
      return state;
  }
}

export default appReducer;